  }
 .content-block {
     position: relative;
    background: #f7f7f7;
    margin: 0px 5px 20px 5px;
    border: 1px solid #e8e8e8;
    height: 500px;
  
  }
  
  .image img {
    width: 100%;
    height: auto;
  }
  
  .description {
    padding: 5px;
  }
  
  .post-heading {
    font:20px;
    font-weight: bold;
    padding: 20px 20px 0px 20px;
    text-decoration: none;
    color: black;
  }
  .text {
    padding: 20px;
  }
  .date {
    width: 100%;
    border-top: 1px solid black;
    border-style: dotted;
    padding: 15px 20px 15px 20px;
    position: absolute; 
    bottom: 0; 
  }
  .next-block {
    margin-right: 5px;
    margin-top: 25px;
    border-top: 1px solid black;
    border-style: dotted;
    padding-top: 25px;
    padding-bottom:25px;
  }
.secondary-headers {
    font-size: 20px;
    font-weight: bold;
  }
  
  .news-content {
    padding-right: 15px;
  }
  
  .date-circle {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    font-size: 22px;
    color: #555555;
    text-align: center;
    background: #ececec;
  }
  
  .header-date {
     line-height: 25px;
     padding: 10px 20px 10px 20px;
  }
  
  .news-name {
    padding-left: 10px;
  }
  
  .news-name h1 {
    font-size: 22px;
    font-weight: bold;
