    border-bottom: 1px solid black;
    border-style: dotted;
  }
  .news-tags {
    padding-left: 10px;
    padding-bottom: 10px;
  
  }
  .news-tags p {
    padding:5px 0;
  
  }
  
  .news-image img {
    width: 100%;
  }
  
  .news-description {
