  }
  
  .recent-post::after {
    content: "";
    clear: both;
    display: block;
  }
  .news-comments {
    padding: 25px 0;
  }
  
  .comments-section {
    border-bottom: 1px solid black;
    border-style: dotted;
    padding: 25px 0;
  }
  .comment {
